import { h } from "preact";
import styled from "styled-components";
import { categoryChoices } from "../../components/helpers/categoryChoices";
import { HeroCTA } from "../../components/helpers/HeroCTA";
import { ProductCategoryTile } from "../../components/helpers/ProductCategoryTile";

const StyledWrapper = styled.div`
	/* height: 55rem; */
`;

const StyledCategoryTileWrapper = styled.div`
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	grid-gap: 1.6rem;
	margin: 3.2rem 2.4rem;
`;

export const HomePage = () => (
	<StyledWrapper>
		<HeroCTA
			ctaTitle="Camping and Storage Supplies"
			heroImage={{
				src: "/assets/images/explore-australia-beach-4wd-sea.jpeg",
				alt: "",
				position: "bottom left",
				size: "cover",
			}}
			handlePrimaryClick="/shop/"
			primaryActionText="Shop now"
		/>

		<StyledCategoryTileWrapper>
			<ProductCategoryTile
				title="Shelf storage"
				href="/shop/drawer"
				backgroundImage={{
					src: "/assets/images/buy-australian-made-camping-storage-underbed-drawer-campervan-caravan-4wd.jpg",
					alt: "tester",
				}}
			/>

			<ProductCategoryTile
				title={"Wood sling"}
				href={"/shop/wood-sling"}
				backgroundImage={{
					src: "/assets/images/camp-fire-wood-carrying-bag-tough-heavy-duty-australian-made-walkers-creek-buy-online-now.jpg",
					alt: "tester",
				}}
			/>
			<ProductCategoryTile
				title={"Caravan Storage"}
				href={"/shop/caravan"}
				backgroundImage={{
					src: "/assets/images/caravan-canvas-and-mesh-storage-pockets-khaki-black-cream.jpg",
					alt: "tester",
				}}
			/>
		</StyledCategoryTileWrapper>
	</StyledWrapper>
);
